/**
 * Styles from https://github.com/react-component/tree
 */
$treePrefixCls: "rc-tree" !default;
$treeNodePrefixCls: "#{$treePrefixCls}-treenode" !default;

.#{$treePrefixCls} {
    margin: 0;
    border: 1px solid transparent;

    &-focused:not(&-active-focused) {
        border-color: #0ff;
    }

    // padding: 5px;
    .#{$treeNodePrefixCls} {
        padding: 0;
        margin: 0;
        line-height: 20px;
        white-space: nowrap;
        list-style: none;
        outline: 0;

        .draggable {
            color: #333;
            user-select: none;
        }

        &.drag-over {
            > .draggable {
                color: #fff;
                background-color: #316ac5;
                border: 1px #316ac5 solid;
                opacity: .8;
            }
        }

        &.drag-over-gap-top {
            > .draggable {
                border-top: 2px #00f solid;
            }
        }

        &.drag-over-gap-bottom {
            > .draggable {
                border-bottom: 2px #00f solid;
            }
        }

        &.filter-node {
            > .#{$treePrefixCls}-node-content-wrapper {
                // stylelint-disable-next-line
                font-weight: 700 !important;
                // stylelint-disable-next-line
                color: #a60000 !important;
            }
        }

        ul {
            padding: 0 0 0 18px;
            margin: 0;
        }

        .#{$treePrefixCls}-node-content-wrapper {
            display: inline-block;
            height: 17px;
            padding: 1px 3px 0 0;
            margin: 0;
            text-decoration: none;
            vertical-align: top;
            cursor: pointer;
        }

        span {

            &.#{$treePrefixCls}-switcher,
            &.#{$treePrefixCls}-checkbox,
            &.#{$treePrefixCls}-iconEle {
                display: inline-block;
                width: 16px;
                height: 16px;
                margin-right: 2px;
                line-height: 16px;
                vertical-align: middle;
                cursor: pointer;
                background-color: transparent;
                background-image: url("data:image/png;base64,");
                background-repeat: no-repeat;
                background-attachment: scroll;
                border: 0 none;
                outline: none;

                &.#{$treePrefixCls}-icon__customize {
                    background-image: none;
                }
            }

            &.#{$treePrefixCls}-icon_loading {
                margin-right: 2px;
                vertical-align: top;
                background: url("data:image/gif;base64,") no-repeat scroll 0 0 transparent;
            }

            &.#{$treePrefixCls}-switcher {
                &.#{$treePrefixCls}-switcher-noop {
                    cursor: auto;
                }

                &.#{$treePrefixCls}-switcher_open {
                    background-position: -93px -56px;
                }

                &.#{$treePrefixCls}-switcher_close {
                    background-position: -75px -56px;
                }
            }

            &.#{$treePrefixCls}-checkbox {
                width: 13px;
                height: 13px;
                margin: 0 3px;
                background-position: 0 0;

                &-checked {
                    background-position: -14px 0;
                }

                &-indeterminate {
                    background-position: -14px -28px;
                }

                &-disabled {
                    background-position: 0 -56px;
                }

                &.#{$treePrefixCls}-checkbox-checked.#{$treePrefixCls}-checkbox-disabled {
                    background-position: -14px -56px;
                }

                &.#{$treePrefixCls}-checkbox-indeterminate.#{$treePrefixCls}-checkbox-disabled {
                    position: relative;
                    background: #ccc;
                    border-radius: 3px;

                    &::after {
                        content: " ";
                        position: absolute;
                        top: 5px;
                        left: 3px;
                        width: 5px;
                        height: 0;
                        border: 2px solid #fff;
                        border-top: 0;
                        border-left: 0;
                        transform: scale(1);
                    }
                }
            }
        }
    }

    &:not(.#{$treePrefixCls}-show-line) {
        .#{$treeNodePrefixCls} {
            .#{$treePrefixCls}-switcher-noop {
                background: none;
            }
        }
    }

    &.#{$treePrefixCls}-show-line {
        .#{$treeNodePrefixCls}:not(:last-child) {
            > ul {
                background: url("data:image/gif;base64,R0lGODlhCQACAIAAAMzMzP///yH5BAEAAAEALAAAAAAJAAIAAAIEjI9pUAA7") 0 0 repeat-y;
            }

            > .#{$treePrefixCls}-switcher-noop {
                background-position: -56px -18px;
            }
        }

        .#{$treeNodePrefixCls}:last-child {
            > .#{$treePrefixCls}-switcher-noop {
                background-position: -56px -36px;
            }
        }
    }

    &-child-tree {
        display: none;

        &-open {
            display: block;
        }
    }

    &-treenode-disabled {

        > span:not(.#{$treePrefixCls}-switcher),
        > a,
        > a span {
            color: #767676;
            cursor: not-allowed;
        }
    }

    &-treenode-active {
        background: rgba(0, 0, 0, .1);
    }

    &-node-selected {
        background-color: #ffe6b0;
        border: 1px #ffb951 solid;
        opacity: .8;
    }

    &-icon__open {
        margin-right: 2px;
        vertical-align: top;
        background-position: -110px -16px;
    }

    &-icon__close {
        margin-right: 2px;
        vertical-align: top;
        background-position: -110px 0;
    }

    &-icon__docu {
        margin-right: 2px;
        vertical-align: top;
        background-position: -110px -32px;
    }

    &-icon__customize {
        margin-right: 2px;
        vertical-align: top;
    }

    &-indent-unit {
        display: inline-block;
        padding-left: 18px;
    }
}
